@import '@/style/mixins';
@import '@/style/var';

.order-table-container {
  padding: 0 16px;
  background: #ffffff;
  border-radius: 4px;
  .box-shadowed();

  .order-table-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 12px 0 12px;

    .table-header-title {
      position: relative;
      margin-bottom: 0;
      padding-left: 13px;
      color: @break-color3;
      font-weight: 500;
      font-size: 14px;
      letter-spacing: 0.4px;

      &::before {
        position: absolute;
        top: 50%;
        left: 0;
        display: block;
        width: 5px;
        height: 20px;
        background: #46a0c0;
        border-radius: 2px;
        transform: translateY(-50%);
        content: '';
      }
    }

    .reload-icon {
      width: 20px;
      height: 20px;
      display: inline-block;
      cursor: pointer;
      .bg-image('~@/assets/images/orderDetail/reload_icon.svg');
    }
  }

  .table-container {
    padding-bottom: 28px;
  }

  .pagination-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 16px 0;
  }
}

.out-put {
  padding: 0 8px 18px 20px;

  .out-title {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    color: @break-color2;
    font-weight: 500;
    font-size: 16px;
  }

  .out-form {
    padding-left: 40px;

    .out-label {
      color: @break-color2;
      line-height: 14px;
      text-align: right;
    }
  }
}

.popover-container {
  width: 348px;
  padding: 18px 14px;

  .popover-title {
    margin-bottom: 30px;
    color: @break-color2;
    font-weight: 500;
    font-size: 18px;
    letter-spacing: 0.77px;
  }

  .popover-wrapper {
    display: flex;

    .popover-label {
      flex-shrink: 0;
      padding-right: 30px;
      color: @break-color3;
      letter-spacing: 0.2px;
      text-align: right;
    }
  }

  .popover-tool {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 40px;
  }
}

.popover-father {
  left: 1108px !important;

  :global(.ant-popover-content .ant-popover-arrow) {
    right: 118px;
  }
}