@import '@/style/var.less';

:global {
  .draggable-modal {
    .dragg-tip-title {
      display: flex;
      align-items: center;
      margin-bottom: 18px;

      .tip-title {
        margin-left: 16px;
        color: @break-color2;
        font-weight: 500;
        font-size: 16px;
      }
    }

    .ant-modal-content {
      .ant-modal-body {
        padding: 32px;
      }

      .ant-modal-footer {
        padding: 0 32px 24px 32px;
        border-top: none;

        .ant-btn+.ant-btn:not(.ant-dropdown-trigger) {
          margin-left: 16px;
        }
      }
    }

    .draggable-modal-order-type {
      position: absolute;
      top: 30px;
      right: -10px;
      width: 70px;
      height: 28px;
      color: #ffffff;
      font-weight: 500;
      font-size: 14px;
      line-height: 28px;
      text-align: center;
      background: @yellow-color;
      border-radius: 4px 4px 0 4px;

      &::after {
        position: absolute;
        top: 20px;
        right: 2px;
        display: block;
        width: 0;
        height: 0;
        border: 8px solid;
        border-color: transparent transparent #b48800;
        transform: rotate(-45deg);
        content: '';
      }
    }
  }
}